.app {
  width: 100%;
  overflow: hidden;
}

.header {
  display: flex;
  height: 100vh;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  background-color: #FF6107;
}
.header_title {
  text-align: left;
  padding: 0;
}

.about {
  display: flex;
  flex-direction: column;
  background-color: #fad3bc;
  padding: 6rem 0;
}
.about_container {
  margin: 3rem 0;
  display: grid;
  grid-template-columns: 30% 70%;
}
.about_title {
  margin-bottom: 3rem;
}
.about_img {
  margin: 0 auto;
  width: 370px;
  height: 500px;
  border-radius: 16px;
}
.about_p_container {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
}
.about_p {
  margin: auto;
  width: 100%;
  font-size: 2rem;
}

.projects {
  padding: 2rem 0;
}
.projects_cards {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}

.card {
  display: grid;
  background-color: #fad3bc;
  border: 2px transparent solid;
  padding: 2rem;
  margin: 2rem;
  border-radius: 16px;
  grid-template-columns: 70% 30%;
  grid-template-rows: 4rem 7rem 4rem;
}
.card_title {
  font-size: 2rem;
  grid-column: 1;
  grid-row: 1;
}
.card_img {
  display: flex;
  justify-self: center;
  align-self: center;
  border-radius: 16px;
  object-fit: contain;
  max-height: 100%;
  max-width: 100%;
  grid-column: 2;
  grid-row: 1/4;
}
.card_description {
  grid-column: 1;
  grid-row: 2;
}
.card_bts {
  grid-column: 1;
  grid-row: 3;
  margin: 0 0 1rem 3rem;
}
.card_button {
  background-color: transparent;
  padding: 1rem;
  border: 2px #FF6107 solid;
  border-radius: 16px;
  font-family: inherit;
  color: #FF6107;
}
.card_link {
  text-decoration: none;
}
.card_link + .card_link {
  margin-left: 3rem;
}
.card_link :hover {
  background-color: #FF6107;
  color: #171615;
}

.contact {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #FF6107;
}
.contact_form {
  display: grid;
  width: 20rem;
  padding: 3rem;
  border-radius: 16px;
}
.contact_input, .contact_textarea {
  margin-bottom: 15px;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  border: none;
  font-size: 1em;
  resize: none;
  border-bottom: 1px solid transparent;
  border-radius: 10px;
  font-family: inherit;
}
.contact_textarea {
  resize: none;
}
.contact_button {
  background-color: transparent;
  padding: 1rem;
  border: 2px #171615 solid;
  border-radius: 16px;
  color: #171615;
}
.contact_button:hover {
  background-color: #f38c51;
}

#mailto {
  display: none;
}

.footer {
  display: flex;
  background-color: #171615;
  justify-content: center;
}
.footer_icon {
  margin: 1rem;
  font-size: 40px;
  color: #FF6107;
}
.footer_icon:hover {
  font-size: 50px;
  color: #f38c51;
}

body {
  font-family: Raleway, sans-serif;
  color: #171615;
}
body h1, body h2 {
  width: 50vw;
  margin: 0 auto;
  font-size: 4rem;
  text-align: center;
  padding: 3rem 0;
}
body::-webkit-scrollbar {
  display: none;
}

